.container-contact {
  text-align: center;
  max-width: 700px;
  margin: 100px auto 0;
}

.container-contact > * {
  margin-bottom: 10px;
}
.container-contact h1 {
  font-size: 50px;
}
.container-contact p:nth-child(4) {
  margin-top: 60px;
}
ul {
  display: flex;
  justify-content: space-around;
  list-style-type: none;
}
ul li {
  font-size: 25px;
  cursor: pointer;
}
