.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: rgba(16, 77, 131, 0.342);

  max-width: 300px;
  min-width: 300px;
  min-height: 200px;
  padding: 10px;
  margin: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  text-align: left;
}
.card h2 {
  font-size: 25px;
  margin-bottom: 10px;
}

.card p {
  font-size: 18px;
}
