nav{
    position: fixed;
    top: 0;
    width: 100%;
    height: 80px;
    background: linear-gradient(45deg, rgb(20, 65, 116),rgb(80, 80, 223),midnightblue);
   
}

.list{
    list-style-type: none;
    background: linear-gradient(45deg, rgb(20, 65, 116),rgb(80, 80, 223),midnightblue);
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
} 
.list a {
    text-decoration: none;
    color: #F1F1F1;
}
.items{
    margin: 0 15px;
    text-transform: uppercase;
    color: #F1F1F1;
    cursor: pointer;
}
.btn {
    display: none;
    width: 40px;
    position: absolute;
    right: 20px;
    top: 17px;
    padding: 5px;
}

@media screen and (max-width: 500px){
.list{
    flex-direction: column;
    height: auto;
    
}

.items{
    height: 70px;
    width: 100%;
    line-height: 70px;
    text-align: center;
}
.btn{
    display: block;
}

} 

